<!--  -->
<template>
    <div class="UserBaseInfo">
        <InputDir ref="name"       label="" @change="name_change"       :val="name"         :require="true" tip="" />
        <InputDir ref="account"    label="" @change="account_change"    :val="account"      :require="true" tip="" v-if="!blnUpdate"/>
        <InputDir ref="pwd"        label="" @change="pwd_change"        :val="pwd"          :require="blnUpdatePwd?false:true" tip=""  v-if="!blnUpdate" type="pwd"/>
        <InputDir ref="confirmPwd" label="" @change="confirmPwd_change" :val="confirmPwd"   :require="blnUpdatePwd?false:true" tip="" v-if="!blnUpdate" type="pwd"/>
        <InputDir ref="depart"     label="" @change="department_change" :val="department"   :require="false" tip=""/>
        <InputDir ref="tel"        label="" @change="tel_change"        :val="tel"          :require="true" reg="^1(3|4|5|7|8)\d{9}$|^(0[0-9]{2,3}-?)?([2-9][0-9]{6,7})+(-[0-9]{1,4})?$" regTip="" tip=""/>
        <InputDir label="" @change="email_change"      :val="email"/>
        <InputDir label="" @change="remark_change"     :val="remark"/>
    </div>
</template>

<script>
import InputDir from '@/components/Input'
export default {
  name: 'UserBaseInfo',
  props:['blnUpdate'],
  components:{InputDir},
  data () {
    return {
      blnAdd:true,
      blnUpdatePwd:false,
      name:'',
      account:'',
      pwd:'',
      confirmPwd:'',
      department:'',
      tel:'',
      email:'',
      remark:'',
    }
  },
  computed:{
    
  },
  methods:{
    Valid(){
      let r=this.$refs,
          res=r.name.Valid(true) &&
              (this.blnUpdate?true:r.account.Valid(true)) &&
              (this.blnUpdate?true:r.pwd.Valid(true)) &&
              (this.blnUpdate?true:r.confirmPwd.Valid(true)) && 
              r.depart.Valid(true) &&
              r.tel.Valid(true) &&
              (this.blnUpdate?true:this.pwd == this.confirmPwd);
      return res;
    },
    triggerChnage(){
      this.$emit('valid');
    },
    name_change(val){this.name=val;this.triggerChnage();},
    account_change(val){this.account=val;this.triggerChnage();},
    pwd_change(val){this.pwd=val;this.triggerChnage();},
    confirmPwd_change(val){
      if(this.blnUpdate)return;
      this.confirmPwd=val;
      if(this.confirmPwd!=this.pwd){
          this.$refs.confirmPwd.setVaildInfo('')
      }else{
          this.$refs.confirmPwd.setVaildInfo('');
      }
      this.triggerChnage();
    },
    department_change(val){this.department=val;this.triggerChnage();},
    tel_change(val){this.tel=val;this.triggerChnage();},
    email_change(val){this.email=val;this.triggerChnage();},
    remark_change(val){this.remark=val;this.triggerChnage();}
  }
}
</script>

<style scoped lang="less">
.UserBaseInfo{width:100%;height:100%;padding:0px 20px;box-sizing: border-box;}

</style>
