/*************************** (.g.js) *****************************/
/* eslint-disable */
import Vue from 'vue'
let mixinContext = require.context('@/directives', false, /\.g\.js$/)

let importAll = (r) => {
    let reg=/([^<>/\\\|:""\*\?]+)\.\w+$/;//

    return r.keys().map(key => { return {name:key.match(reg)[1].replace('.g',''),key,r}});
}

export default{
    register(){
        importAll(mixinContext).forEach(m=>Vue.directive(m.name,m.r(m.key).default));
    }
}