/*************************** (.g.js) *****************************/

import Vue from 'vue'
let mixinContext = require.context('@/mixins', false, /\.g\.js$/)

let importAll = (r) => {
  return r.keys().map(key => r(key));
}

export default{
    register(){
        importAll(mixinContext).forEach(m=>Vue.mixin(m.default));
    }
}